import { Box, Toolbar, Typography, Button, CssBaseline } from '@mui/material';
import AppBar from '@mui/material/AppBar';

function DropImage(props) {
  const dropFile = (event) => {
    event.preventDefault()

    let url;
    if (event.dataTransfer.items) {
      url = URL.createObjectURL(event.dataTransfer.items[0].getAsFile())
    } else {
      url = URL.createObjectURL(event.dataTransfer.files[0].getAsFile())
    }

    if (url) {
      props.onChange(url)
    }
  }

  const uploadFile = (event) => {
    const file = event.target.files[0];
    const url = URL.createObjectURL(file)

    props.onChange(url)
  }

  return (
    <Box display={'flex'} flexDirection='column'>
      <CssBaseline />
      <AppBar component={'nav'} position='static'>
        <Toolbar>
          <Typography variant='h6' sx={{ flexGrow: 1 }}>Разметка изображений</Typography>
          <Button color='inherit' component="label">
            Загрузить
            <input
              id="img-input"
              accept='image/png, image/jpeg'
              type="file"
              onChange={uploadFile}
              hidden
            />
          </Button>
        </Toolbar>
      </AppBar>
      <Box component={'label'} for='img-input' onDrop={dropFile} onDragOver={(e) => e.preventDefault()}>
        <Box padding='40vh' margin={'12px'} border='2px dashed #ACACAC' borderRadius={'12px'}>
          <Typography component={'div'} width='max-content' margin={'auto'}>Перетащите файл для загрузки</Typography>
        </Box>
      </Box>
    </Box>
  )
}

export default DropImage
