import { Annotorious } from '@recogito/annotorious'
import { useEffect, useRef, useState } from 'react';
import { Box, Toolbar, Typography, Button, CssBaseline } from '@mui/material';
import AppBar from '@mui/material/AppBar';

import '@recogito/annotorious/dist/annotorious.min.css';

function EditAnnotations(props) {
  const imgEl = useRef();

  const [anno, setAnno] = useState();
  const [positon, setPosition] = useState({ top: 0, left: 0, x: 0, y: 0 });

  const startDrag = (event) => {
    if (event.button !== 1) {
      return
    }

    const elem = document.getElementById('wrapper');

    event.preventDefault()
    setPosition({
      left: elem.scrollLeft,
      top: elem.scrollTop,
      x: event.clientX,
      y: event.clientY,
    })
  }

  const mouseMoveHandler = (event) => {
    if (positon.x === 0 && positon.y === 0) {
      return
    }

    const elem = document.getElementById('wrapper');

    const dx = event.clientX - positon.x;
    const dy = event.clientY - positon.y;

    elem.scrollTop = positon.top - dy;
    elem.scrollLeft = positon.left - dx;
  }

  const mouseUpHandler = function (event) {
    if ((positon.x === 0 && positon.y === 0) || event.button !== 1) {
      return
    }

    setPosition({ top: 0, left: 0, x: 0, y: 0 })
  }

  useEffect(() => {
    let annotorious = null;

    if (imgEl.current) {
      console.log(imgEl.current.naturalHeight)
      annotorious = new Annotorious({
        image: imgEl.current,
        allowEmpty: true,
        drawOnSingleClick: true,
        widgets: [
          // eslint-disable-next-line
          recogito.HelloWorldWidget
        ]
      });

      annotorious.setDrawingTool('polygon');

      annotorious.on('createAnnotation', annotation => {
        console.log('created', annotation);
      });
    }

    setAnno(annotorious);

    return () => annotorious.destroy();
  }, []);

  const sendData = () => {
    const data = {
      paths: Object.values(anno.getAnnotations().map((annotation, i) => {
        const path = annotation.target.selector.value.split('"')[1].split(' ').map((coords) => coords.split(','))
        path.push(path[0])

        console.log(i, annotation)

        return {
          dots: path,
          level: i,
          filename: annotation.body[0].value.audio,
          text: annotation.body[0].value.text
        }
      }))
    }
    data.size = {
      height: imgEl.current.naturalHeight,
      width: imgEl.current.naturalWidth
    }
    console.log(data)

    fetch('http://host/polygons', {
      method: 'POST',
      body: data
    }).then(() => {
      props.onChange()
    })

  }

  return (
    <Box display={'flex'} flexDirection='column'>
      <CssBaseline />
      <AppBar component={'nav'}>
        <Toolbar>
          <Typography variant='h6' sx={{ flexGrow: 1 }}>Разметка изображений</Typography>
          <Button color='inherit' component="label" onClick={sendData}>
            Сохранить
          </Button>
        </Toolbar>
      </AppBar>
      <Box
        id='wrapper'
        width={'100vw'}
        height={'100vh'}
        overflow={'auto'}
        paddingTop={'64px'}
        onMouseDown={startDrag}
        onMouseMove={mouseMoveHandler}
        onMouseUp={mouseUpHandler}
      >
        <img
          ref={imgEl}
          src={props.url}
          alt="img"
        />
      </Box>
    </Box>
  )
}

export default EditAnnotations
