import './App.css';

import { useState } from 'react';
import DropImage from './steps/DropImage'
import EditAnnotations from './steps/EditAnnotations'

function App() {
  const [step, setStep] = useState('add')
  const [url, setUrl] = useState('')

  return (
    <div>
      {step === 'add' ? (
        <DropImage onChange={(_url) => { setUrl(_url); setStep('edit') }}></DropImage>
      ) : (
        <EditAnnotations url={url} onChange={() => { setUrl(''); setStep('add') }}></EditAnnotations>
      )}

    </div>
  );
}

export default App;
